.App {
  height: 100vh;
  background-color: #202020;
  color: #eee;
  font-family: Raleway;
  font-size: 1rem;
}

.content-container {
  display: grid;
  place-items: center;
}

.content-section {
  display: flex;
  place-items: center;
  width: min(80%, 800px);
}

h2 {
  font-family: Droid Sans Mono;
  /* color: #4CAF51; */
  color: #0099AA;
}

a {
  text-decoration-color: #999;
  color: white;
}

a:hover {
  text-decoration-color: #0099AA;
  color: #0099AA;
}